@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0;
  background-color: #f05d1a;
}

.App {
  font-family: "Lato", sans-serif;
  box-sizing: border-box;
  text-align: center;
  max-width: 768px;
  box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.75);
  margin: auto;
  padding-top: 32px;
  padding-bottom: 32px;
  background-color: #f2f2ee;
}

.big-header {
  font-size: 4em;
  margin: 0;
  margin-bottom: 16px;
}

.explainer {
  max-width: 400px;
  margin: auto;
  margin-bottom: 32px;
  padding-left: 8px;
  padding-right: 8px;
}

.input-container {
  max-width: 300px;
  margin: auto;
  display: flex;
  flex-direction: column;
  padding-top: 8px;
  padding-bottom: 8px;
}

.split-input-container {
  flex-direction: row;
}

.split-input-divider {
  width: 50%;
}

.input-label {
  width: 100%;
  text-align: left;
}

.input-element {
  width: 100%;
}
